.portfolio__figure:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  transition: all 0.3s cubic-bezier(0.72, -0.04, 0.14, 1.03);
}
