.overlay {
  position: fixed;
  width: 100vw;
  height: 105vh;
  top: 60px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(15px);
  backface-visibility: hidden;
  transition: all 0.3s cubic-bezier(0.72, -0.04, 0.14, 1.03);
}
@media only screen and (min-width: 992px) {
  .overlay {
    position: inherit;
    top: inherit;
    width: inherit;
    height: inherit;
    background: inherit;
    overflow-y: inherit;
    opacity: 1;
    transform: none;
    visibility: visible;
    pointer-events: auto;
  }
}
.overlay__wrap {
