.burger {
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  z-index: 1000;
  background: transparent;
  border: none;
  outline: none;
  display: block;
  z-index: 100;
}
.burger:hover {
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}
.burger i {
  display: block;
  width: 30px;
  height: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before,
.burger i:after {
  content: '';
  position: absolute;
  height: 3px;
  width: 30px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before {
  margin-top: -12px;
}
.burger i:after {
  margin-top: 12px;
}
.burger.active i {
  background: transparent;
}
.burger.active i:before,
.burger.active i:after {
  left: 0;
  width: 25px;
}
.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}
.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  top: 152px;
  left: -50px;
}
.slick-next {
  top: 213px;
  left: -50px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: -48px;
  top: 50%;
  transform: translateY(17%);
  border-image: transparent;
}
.slick-dots li {
  margin: 4px 8px;
  margin-bottom: 10px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 13px;
  width: 13px;
  font-size: 0px;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
